footer{
    display: block;
    padding-top: 50px;
    gap: 50px;
    width: 100%;
    background-image: url("../../fondd.jpg");
}

.foot{
    display: flex;
}


@media  screen and (max-width: 500px){
    .contact p{
        display: flex;
        margin-left: 17px ;
        font-size: 12px;
    }
    .contact{
        WIDTH: 100%;
    }
}

@media  screen and (max-width: 800px){
    .contact{
        margin-left: auto;
        WIDTH: 36%;
    }
    .contact p{
        display: flex;
        margin-left: 17px ;
        font-size: 16px;
    }
}

@media screen and (min-width: 1000px){
    .contact{
        margin-left: auto;
        WIDTH: 30%;
    }
    .contact p{
        display: flex;
        margin-left: 17px ;
        font-size: 16px;
    }
}





.tel{
    display: flex;
    width: 100%;
    height: 25px;
}

.tel img{
    WIDTH: 36px;
    height: 36px;
}

.mail{
    display: flex;
    width: 25%;
    height: 52px;
    margin-top: 20px;
}

.mail img{
    width: 39px;
    height: 39px;
}


footer div{
    margin-top: auto;
    margin-bottom: auto;   
   }

.logo{
    width: 137px;
    height:137px;
    margin-left: 20px;
    display: block;
}